		methods: {
			DecimalToTwoBytesLowByteFirst(decimalNumber) {
				// 将十进制数转换为十六进制字符串  
				const hexString = decimalNumber.toString(16).toUpperCase()

				console.log('16进制', hexString)

				// 如果十六进制字符串长度不足2，则在前面补0  
				const paddedHexString = hexString.padStart(2, '0')

				console.log('补0结果', paddedHexString)

				var lowByte = 0
				var highByte = 0
				if (paddedHexString.length > 2) {
					highByte = parseInt(paddedHexString.substring(0, 2), 16)
					lowByte = parseInt(paddedHexString.substring(2), 16)
				} else {
					lowByte = parseInt(paddedHexString.substring(0, 2), 16)
					highByte = 0
				}

				//    // 将十六进制字符串转换为字节数组，低字节在前  
				//    const lowByte = parseInt(paddedHexString, 16) 

				// console.log('低字节', lowByte)

				//    const highByte = parseInt(paddedHexString.charAt(0), 16)

				// console.log('高字节', lowByte)

				console.log(lowByte, highByte)
				return [lowByte, highByte]
			},
			XOR(data, len) {
				let xorResult = 0;
				for (let i = 0; i < len; i++) {
					xorResult ^= data[i];
				}

				return xorResult;
			},
			CRC16(data, dataLength) {
				var len = dataLength
				if (len > 0) {
					var crc = 0
					for (var i = 0; i < dataLength; i++) {
						crc = this.UpdateCRC16(crc, data[i]);
					}
					crc = this.UpdateCRC16(crc, 0);
					crc = this.UpdateCRC16(crc, 0);

					var hi = (crc & 0xFF00) >> 8 //高位置
					var lo = crc & 0x00FF //低位置

					return new Uint8Array([hi, lo])
				}
				return new Uint8Array([0, 0])
			},
			UpdateCRC16(crcIn, bytee) {
				var crc = crcIn;
				var ins = bytee | 0x100;

				do {
					crc <<= 1;
					ins <<= 1;
					if ((ins & 0x100) == 0x100) {
						++crc;
					}
					if ((crc & 0x10000) == 0x10000) {
						crc ^= 0x1021;
					}
				}
				while (!((ins & 0x10000) == 0x10000));
				return crc;
			},
			ToInt16(value, startIndex) {
				// 确保startIndex是偶数，因为int16占用2个字节  
				if (startIndex % 2 !== 0) {
					throw new Error('startIndex must be an even number');
				}

				// 获取两个字节  
				let byte1 = value[startIndex];
				let byte2 = value[startIndex + 1];

				// 将字节转换为16位整数  
				// 注意：JavaScript的位操作默认是有符号的，因此不需要额外的转换  
				let int16 = (byte1 << 8) | byte2;

				return int16;
			},
			testClick() {
				this.test = {
					value: '12345',
					change: this.test.change
				}
			},
			// test(){
			// 	console.log('哈哈哈')
			// },
			// 反向映射（仅当枚举值是字符串时有效）
			getValueName(data, value) {
				for (const name in data) {
					//console.log(name)
					if (data[name] === value) {
						console.log(name)
						return name
					}
				}

				return undefined;
			},
			GetBitValue(data, pos) {
				if ((data & (1 << pos)) > 0) //按位与之后的结果非0
				{
					console.log('1')
					return 1;
				} else {
					console.log('0')
					return 0;
				}
			},
			// 导出
			exportData() {
				this.test = '啊哈哈哈'
				return

				uni.downloadFile({
					//下载
					url: 'https://localhost:44301/ExcelTemplate/FullBoxRecord.xls',
					header: {
						'content-type': 'application/vnd.ms-excel'
					},
					success(res) {
						console.log(res);
						if (res.statusCode === 200) {
							let savePath = wx.env.USER_DATA_PATH + '/DriverFullBoxRecord_' + Date.now() + '.xls';
							uni.getFileSystemManager().saveFile({
								//下载成功后保存到本地
								tempFilePath: res.tempFilePath,
								filePath: savePath,
								success(res) {
									uni.hideLoading();
									console.log(res);
									let savePath = res.savedFilePath;
									uni.showModal({
										title: "下载成功",
										content: "是否打开?",
										confirmColor: "#0bc183",
										confirmText: "打开",
										success(res) {
											if (res.confirm) {
												uni.openDocument({
													//打开
													filePath: savePath,
													showMenu: true,
													success(res) {
														console.log(res);
													},
												});
											} else if (res.cancel) {}
										},
									});
								},
								fail(err) {
									uni.hideLoading();
									uni.showModal({
										title: '提示',
										content: "文件下载失败",
										showCancel: false, //是否显示取消按钮
										success: function(result) {}
									})
								},
							});
						} else {
							uni.hideLoading();
							uni.showModal({
								title: '提示',
								content: "文件下载失败",
								showCancel: false, //是否显示取消按钮
								success: function(result) {}
							})
						}
					},
					fail(err) {
						uni.hideLoading();
						uni.showModal({
							title: '提示',
							content: "文件下载失败",
							showCancel: false, //是否显示取消按钮
							success: function(result) {}
						})
					}
				});
			},
			comboxInput(e) {
				console.log(e)
			},
			previewImage() {
				uni.previewImage({
					urls: [this.minImage],
					current: this.minImage
				})
			},
			// 二维码生成工具
			qrR(res) {
				this.src = res
			},
			makeCode() {
				uni.getScreenBrightness({
					success: function(res) {
						console.log(res)
						this.screenBrightness = res.value
					}
				})
				console.log(this.$refs['qrcode'])
				this.$refs.qrcode._makeCode();
				uni.setScreenBrightness({
					value: 1,
					success() {
						console.log('设置亮度成功')
					}
				})
			},
			clearCode() {
				this.$refs.qrcode._clearCode();
			},
			saveCode() {
				this.$refs.qrcode._saveCode();
			},
			scanCode() {
				uni.scanCode({
					onlyFromCamera: true,
					success(res) {
						console.log(res)
						console.log(JSON.parse(res.result).type)
					}
				})
			},
			register() {
				var BaseStaff = {
					'BaseStaff': {
						'StaffGuid': this.generateUUID(),
						'StaffName': '小程序员工',
						'StaffRole': '驾驶员',
						'StaffNum': '20230127',
						'StaffCardID': '2430375845',
						'DataSource': '小程序注册',
						'TenantId': 2
					}
				}
				this.$Api.Staff_CreateOrUpdate(JSON.stringify(BaseStaff))
			},
			generateUUID() {
				var d = new Date().getTime();
				var uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
					var r = (d + Math.random() * 16) % 16 | 0;
					d = Math.floor(d / 16);
					return (c == 'x' ? r : (r & 0x3 | 0x8)).toString(16);
				});
				return uuid;
			},
			//构造2万条数据
			getAllList() {
				for (let i = 0; i < 20000; i++) {
					this.listAll.push(`我是第${i}号佳丽`)
				}
			},

			//计算可视区域数据
			getShowList() {
				this.startIndex = Math.floor(this.scrollTop / this.itemHeight); //可视区域第一条数据的索引
				this.endIndex = this.startIndex + this.showNum; //可视区域最后一条数据的后面那条数据的索引
				this.showList = this.listAll.slice(this.startIndex, this.endIndex +
					5) //可视区域显示的数据，即最后要渲染的数据。实际的数据索引是从this.startIndex到this.endIndex-1
				this.top = this.scrollTop - (this.scrollTop % this
					.itemHeight); //在这需要获得一个可以被itemHeight整除的数来作为item的偏移量，这样随机滑动时第一条数据都是完整显示的
			},

			//区域滚动事件
			scrollEvent(e) {
				this.scrollTop = e.detail.scrollTop
				this.getShowList()
			}


		}

	}
</script>

